
// 3 COLS - page is limited to 1400px, centered, not growing
$MAX_BIG_SCREEN: 1400px;
// scalable - 3 COLS    range: [9px -> 14px]
$MIN_BIG_SCREEN: 1000px;
// 1COL - page is limited to 500px, centered, not growing
$MAX_SMALL_SCREEN: 500px; // range: [... -> 15px]
// 1COL - scalable



// :root { --big-screen-switch: 1; }

html,
body {    
    --big-screen-switch: 1;
    --small-screen-switch: 0;    
    --small-screen-mult-05: 1;    

    font-size: 14px;    

    .page-item {
        position: relative;
        // border: 1px solid orange;
        width: 100%;
        box-sizing: border-box;
        
        .page-item-content {
            position: relative;
            // border: 1px solid red;
            box-sizing: border-box;
            width: 100rem;            

            padding-left: 4rem;
            padding-right: 4rem;    
        }
    }     
}


.page-item.two-column-content {
    .page-item-content {
        max-width: 1400px;
    }
}

.layout.horizontal-big-screen {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    -ms-flex-direction: row;
    -webkit-flex-direction: row;
    flex-direction: row;
}

.layout.center-big-screen {
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.flex-big-screen {
    -ms-flex: 1 1 0.000000001px;
    -webkit-flex: 1;
    flex: 1;
    -webkit-flex-basis: 0.000000001px;
    flex-basis: 0.000000001px;
}

@media (max-width: $MAX_BIG_SCREEN) {

    html,
    body {
        font-size: 1vw;
    }
}

@media (max-width: $MIN_BIG_SCREEN) {    

    html,
    body {
        --big-screen-switch: 0;
        --small-screen-switch: 1;
        --small-screen-mult-05: 0.5;

        font-size: 15px;

        .page-item {
            .page-item-content {
                width: 33rem;
            }
        }
    }

    .hide-small-screen {
        display: none;
    }

    .layout.horizontal-big-screen {
        -ms-flex-direction: column;
        -webkit-flex-direction: column;
        flex-direction: column;
    }

    .layout.center-big-screen {
        -ms-flex-align: unset;
        -webkit-align-items: unset;
        align-items: unset;
    }    

    .flex-big-screen {
        -ms-flex: unset;
        -webkit-flex: unset;
        flex: unset;
        -webkit-flex-basis: unset;
        flex-basis: unset;
    }    
}

.card123 {
    padding: 2rem;
    // border: 1px solid blue;

    ._img {
        width: 100%;
    }

    ._header {
        font-size: 1.5rem;
        font-weight: 900;
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
    }

    ._main {
        font-size: 1.35rem;
        font-weight: 300;
    }
}

@media (max-width: $MAX_SMALL_SCREEN) {

    html,
    body {
        font-size: 3vw;
    }
}


@mixin debugBorder($color) {
    // border: 4px solid $color;
}


.section-header {    
    margin-top: 8rem;
    margin-bottom: 1rem;

