@import "vars";


////////////////////////////////////////////////////////////////

.site-titles {
    .site-title1 { font-size: 3rem; line-height: 120%; }
    .site-title2 { font-size: 1.25rem; line-height: 120%; }
    .site-title3 { font-size: 1.85rem; line-height: 120%; }
    .site-title4 { font-size: 1.25rem; line-height: 120%; }
}

@media (max-width: $MIN_BIG_SCREEN) {  
    .site-titles {
        .site-title1 { font-size: 2rem; }
        .site-title2 { font-size: 1.1rem; }
        .site-title3 { font-size: 1.4rem; }
        .site-title4 { font-size: 1.1rem; }
    }    
}


////////////////////////////////////////////////////////////////

.site-titles {
    color: #222;

    .site-title1 {
        font-weight: 900;
        // color: hsl(218, 54%, 37%);        
    }

    .site-title2 {
        .strong-text {
            font-weight: 700;
        }        
    }

    .site-title3 {
        font-weight: 900;
        .strong-text {
            font-weight: 900;
        }        
    }

    .site-title4 {
        .strong-text {
            font-weight: 900;
        }        
    }
}

////////////////////////////////////////////////////////////////


.site-titles {
    z-index: 9;
    text-align: center;
}

//Buttons - Stores

.store-buttons {
    display: flex;

    &._with-shadow a.store-button {
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
    }

    a.store-button {
        text-decoration: none;
    }

    .store-button {
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    
        font-size: 14px;
        color: #fff;
        background: rgb(0, 102, 204);
        border-radius: 8px;
        // border: 1px solid #fff;        

        display: inline-block;
        height: 42px;
        padding-left: 4px;
        padding-right: 8px;
        cursor: pointer;
        user-select: none;

        svg {
            height: 24px;
            width: 24px;
            margin-right: 4px;
        }
    }

    .store-button:not(:first-child) {
        margin-left: 8px;
    }
}


