@import "vars";
@import "layout";


.product-title {
    font-size: 28px; // NEEDS TO BE IN PX
    color: #fff;
    line-height: 1;
}

.menu-item {
    font-size: 18px; // NEEDS TO BE IN PX
    color: #fff;
}

.site-header {
    box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.25); 
    position: fixed; 
    top: 0; 
    left: 0; 
    right: 0; 
    width: 100vw; 
    height: 42px; 
    padding-left: 2rem; 
    padding-right: 2rem; 
    box-sizing: border-box; 
    background: #222;
    color: #fff;
    padding-top:1.2rem;
    padding-bottom:1.2rem;
    z-index: 10;
    justify-content: center;

    a {
        text-decoration: none;
    }
    .section{
        max-width: calc(min(60em, 90%));
    }
}

.site-footer {
    font-size: 1em; 
    width: 100%; 
    // height: 42px; 
    background: #222; 
    color: #fff;
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;

    a {
        text-decoration: none;
    } 
}

.pxd-address {
    padding-top: 1rem;
    padding-bottom: 1rem;
}

////////////////////////////////////////////////////////////////

.pricing-section {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 4rem;
    margin-bottom: 16rem;
    margin-left: 2rem;
    margin-right: 2rem;


    .pricing-section-content {
        display: flex;
        flex-direction: column;
        align-items: start;


        .pricing-title {
            margin-top: 3rem;
            margin-bottom: 1rem;
            color: #333;

            ._l1 {
                font-weight: 700;
                font-size: 4rem;
                // line-height: 150% !important;
            }

            ._l2 {
                font-size: 3rem;
                font-weight: 400;

                .strong-text {
                    font-weight: 700;
                }

                margin-top: 1rem;
                // line-height: 150% !important;
            }

            ._l3 {
                font-size: 2.5rem;
                font-weight: 400;

                .strong-text {
                    font-weight: 700;
                }

                margin-top: 1.2rem;
                // line-height: 150% !important;
            }
        }

        .free-pricing-title {
            margin-top: 3rem;
            margin-bottom: 1rem;
            color: #333;

            ._l1 {
                font-weight: 400;
                font-size: 2rem;
                // line-height: 150% !important;

                .strong-text {
                    font-weight: 900;
                }
            }
            ._l2 {
                margin-top: 1rem;
                font-weight: 400;
                font-size: 1.25rem;
                max-width: 50rem;
                line-height: 150% !important;

                .strong-text {
                    font-weight: 600;
                }
            }
        }

        .pricing-title2 {
            margin-top: 1.5rem;
            margin-bottom: 1rem;
            font-size: 1.2rem;
        }

        .pricing-features-list {
            margin-top: 2rem;
            margin-bottom: 3rem;
            max-width: 800px;
            font-weight: 400;
            font-size: 1.5rem;

            li {
                line-height: 150%;
            }
        }
    }
}

@media (max-width: $MIN_BIG_SCREEN) {

    .pricing-section {
        .pricing-section-content {
            .pricing-title { 
                ._l1 { font-size: 1.5rem; }
                ._l2 { font-size: 1.2rem; }
                ._l3 { font-size: 1.2rem; }
            }
            .free-pricing-title {
                ._l1 { font-size: 1.2rem; }
                ._l2 { font-size: 1rem; }
            }
            .pricing-title2 { font-size: 1rem; }
            .pricing-features-list { font-size: 1rem; }            
        }
    }
}